/** Text Colors **/
:root {
  --text-main: #3E3F42;
  --text-adverse-main: white;
  --text-base: #6B6C6F;
  --text-adverse-base: #FBFBFD;
  --text-secondary: #9EA0A5;
  --text-tertiary: #CECFD2;
  --border-color-base: #EAEDF3;
  --border-color-main: #D8DCE6;
  --border-color-adverse: #2E2E33;
  --border-color-secondary: #2D9C3C;
  --bg-base: #FBFBFD;
  --bg-primary: #FFFFFF;
  --bg-adverse-primary: #252529;
  --color-danger: #E6492D;
  --color-danger-hex: 230, 73, 45;
  --color-primary: #1665D8;
  --color-primary-hex: 22, 101, 216;
  --color-secondary: #38B249;
  --color-secondary-hex: 56, 178, 73;
  --color-success: #38B249;
  --color-success-hex: 56, 178, 73;
  --color-warning: #F6AB2F;
  --color-warning-hex: 246, 171, 47;
  --gradient-secondary: linear-gradient(0deg, #34AA44 0%, #38B249 100%); }

*,
::after,
::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

html {
  height: 100%;
  background: var(--bg-base); }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  font-family: "Roboto", sans-serif;
  background: var(--bg-base);
  color: var(--text-base);
  font-size: 16px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body input,
  body button,
  body textarea {
    color: var(--text-main);
    font-size: 16px;
    font-family: "Roboto", sans-serif; }
  body h1,
  body h2,
  body h3,
  body h4,
  body h5,
  body h6 {
    transition: all 0.2s;
    margin: 0;
    line-height: 1.5;
    font-weight: 400;
    color: var(--text-main); }
  body * + h1,
  body * + h2,
  body * + h3,
  body * + h4,
  body * + h5 {
    margin-top: 1.125rem; }
  body h1 + *,
  body h2 + *,
  body h3 + *,
  body h4 + * {
    margin-top: 1em; }
  body p {
    transition: all 0.2s;
    margin: 0;
    line-height: 1.375rem;
    font-weight: 400;
    font-size: 0.875rem;
    color: var(--text-base); }
  body input,
  body textarea {
    font-size: .875rem; }
  body * + p {
    margin-top: 0.3em; }
  body h1 {
    font-size: 2.25rem;
    font-weight: 600; }
  body h2 {
    font-size: 1.375rem;
    line-height: 2rem; }
  body h3 {
    font-size: 1.125rem; }
  body h4 {
    font-size: 1.1rem; }
  body p + *,
  body ul + *,
  body ol + * {
    margin-top: 1.125em; }
  body p + p {
    margin-top: 0.4em; }
  body a {
    transition: .2s;
    text-decoration: none;
    cursor: pointer; }
    body a:hover {
      text-decoration: none; }
  body p a,
  body ul a {
    display: inline-block;
    position: relative;
    transition: all 0.2s;
    color: #1665D8; }
    body p a:hover,
    body ul a:hover {
      text-decoration: underline; }
  body strong {
    font-weight: 500; }
  body ul,
  body ol {
    line-height: 1.5;
    color: var(--text-main);
    font-size: 0.875rem; }
    body ul *:not(:last-child),
    body ol *:not(:last-child) {
      margin-bottom: 8px; }
  body ol {
    padding-left: 20px; }
  body ul:global(.no-style) {
    list-style: none;
    padding-left: 0; }
    body ul:global(.no-style) ul {
      padding-left: 20px; }
  body code:not([class]) {
    display: inline-block;
    border: 1px solid #DADEE7;
    border-radius: 4px;
    font-size: inherit;
    line-height: 1.375rem;
    padding: 0px 5px;
    font-family: monospace;
    word-break: inherit;
    color: var(--color-primary);
    margin-left: 1px;
    margin-right: 1px; }

:global #root {
  height: 100%; }

pre {
  margin-bottom: 0; }

label {
  font-weight: 500;
  font-size: .75rem;
  color: var(--text-secondary);
  line-height: 1.125rem;
  text-transform: uppercase;
  margin: 0; }
  label + * {
    margin-top: .5rem; }

.icons {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 0;
  justify-content: space-evenly; }
  .icons > * {
    flex-basis: 250px; }

.iconWrapper {
  display: flex;
  height: 90px;
  align-items: center; }

.icon {
  flex-basis: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 0; }

.ligature {
  font-size: .875rem;
  color: var(--text-main); }
